.wrapper {
  padding: 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: thistle;
}
.wrapperLogo {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 5px;
  font-family: 'DM-Sans';
  font-size: 32px;
  font-style: normal;
  font-weight: 700;
  color: var(--primary-black);
}
.userBlock {
  margin-left: auto;
  margin-right: 10px;
  display: flex;
  align-items: center;
  gap: 15px;
}
.cart {
  width: 48px;
  height: 48px;
}
.avatar {
  width: 48px;
  height: 48px;
}
.avatarImg {
  width: 48px;
  height: 48px;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: cover;
}
.avatarName {
  max-width: 150px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-family: 'DM-Sans';
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
}
.wrapperCart {
  position: relative;
}
.wrapperCounterCart {
  position: absolute;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.9);
  top: 10px;
  left: 50%;
  transform: translate(-50%);
  font-family: 'DM-Sans';
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  color: var(--primary-black);
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}


.dark {
  background-color: #23272f;
  color: white;
--primary-black: white;
  border-bottom: 1px solid rgb(60, 60, 60);
}

.light {
   background: thistle;
}
.dark .imgTheme{
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 42px;
  height: 42px;
  border-radius: 50px;
  background-color: white;
}
.dark .counterCart{
  color: black;
}
